@import "utilities/variables";

.location {
  display: flex;
  position: relative;
  z-index: 1;
  justify-content: center;
  height: 50px;
  align-items: center;
  background-color: $location-background-color;
  margin-bottom: 0px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, .25);
  &__btn {
    position: relative;
    width: 12px;
    height: 16px;
    background-color: transparent;
  }
  &__txt {
    background-color: transparent;
    font-family: $font-default;
    font-size: 16px;
    font-weight: 500;
    height: 19px;
    padding-left: 17px;
    
  }
  &__icon {
    position: absolute;
    background-color: transparent;
    color: $light-theme-main-text-color;
    right: 0px;
    top: 0px;
    width: 12px;
    height: 16px;
  }
}